.icon-avatar {
  /* align-items: center; */
  border: 4px solid;
  border-radius: 50%;
  display: flex;
  height: 80px;
  justify-content: center;
  min-height: 80px;
  min-width: 80px;
  overflow: hidden;
  position: relative;
  width: 80px;
  margin: 20px auto;
}

.icon-avatar .fa-cog {
  color: transparent;
  position: absolute;
  transition: color 0.3s ease-in;
}

.icon-avatar:hover .fa-cog {
  color: darksalmon;
}

.icon-avatar:hover .fa-cog:hover {
  color: forestgreen;
}
